import { Redirect, Route } from 'react-router-dom';
import {IonApp, IonIcon, IonLabel, IonRouterOutlet, IonTabBar, IonTabButton, IonTabs, IonButton, IonRedirect} from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import { book, calculator, time, business, ban } from 'ionicons/icons';
import Tab1 from '../pages/Tab1';
import Tab2 from '../pages/Tab2';
import Tab3 from '../pages/Tab3';
import Tab4 from '../pages/Tab4';
import App from '../App';

import './Main.css';

/* Theme variables */
import { useAuth0 } from '@auth0/auth0-react';
import { NONAME } from 'dns';

const Main: React.FC = () => {
    const {user, isAuthenticated } = useAuth0();
    const { logout } = useAuth0();

    return(
    <IonApp>
      <IonReactRouter>
        
        <IonTabs>
          <IonRouterOutlet>
            <Route exact path="/Tab1">
              <Tab1 />
            </Route>
            <Route exact path="/tab2">
              <Tab2 />
            </Route>
            <Route exact path="/tab3">
              <Tab3 />
            </Route>
            <Route exact path="/tab4">
              <Tab4 />
            </Route>
            
          </IonRouterOutlet>
          <IonTabBar slot="bottom">
            <IonTabButton tab="tab1" href="/tab1">
              <IonIcon icon={time} />
              <IonLabel>Cashiers</IonLabel>
            </IonTabButton>
            <IonTabButton tab="tab2" href="/tab2">
              <IonIcon icon={calculator} />
              <IonLabel>Lottery</IonLabel>
            </IonTabButton>
            <IonTabButton tab="tab3" href="/tab3">
              <IonIcon icon={business} />
              <IonLabel>Office</IonLabel>
            </IonTabButton>
            <IonTabButton tab="tab4" href="/tab4">
              <IonIcon icon={book} />
              <IonLabel>Notes</IonLabel>
            </IonTabButton>
            <IonTabButton >
                <IonButton onClick={() => logout()} color="none" fill="clear" size="small" href="/scheduling-application">
                    <IonIcon icon={ban} />
                    <IonLabel >Logout</IonLabel>
                </IonButton>
            </IonTabButton>
          </IonTabBar>
        </IonTabs>
      </IonReactRouter>
    </IonApp>
    );
};
  
  export default Main;